.consultant {
  &__wrapper {
    display: grid;
    grid-template: auto / 1fr 720px 285px 1fr;
  }
  &__call {
    text-align: center;
    max-width: 722px;
    padding: 40px 50px 45px 0;
    position: relative;
    background-color: #eaebec;
  }
  &__text {
    font-size: 15px;
    font-weight: 300;
  }
  &__number {
    font-size: 18px;
    line-height: 35px;
    padding: 15px 0 20px;
    border: 1px double #000;
    font-family: 'futuraround';
    text-transform: uppercase;
    letter-spacing: 2px;
    width: 80%;
    margin: 30px auto 0;
    a {
      font-size: 31px;
      color: $color-text;
      text-decoration: none;
    }
  }
  &__light-text {
    font-size: 29px;
    font-style: italic;
    color: $color-light-text;
    margin: 30px 0 0;
    div {
      font-size: 12px;
    }
  }
  &__form {
    margin: 30px 0 0;
    display: flex;
    justify-content: center;
  }
  &__btn {
    width: 112px;
    background-color: $color-main;
    border-radius: 7px;
    border: none;
    padding: 10px 13px;
    font-size: 11px;
    font-weight: 900;
    text-transform: uppercase;
  }
  &__text-reaction {
    margin: 30px 0 0;
    font-weight: 300;
    font-size: 12px;
  }
  &__headphones {
    position: absolute;
    right: -106px;
    top: 50%;
    transform: translateY(-50%);
  }
  &__micro {
    position: absolute;
    right: -66px;
    top: 63.4%;
    transform: translateY(-50%);
    z-index: 5;
  }
  &__consultant {
    text-align: center;
    max-width: 283px;
    padding: 40px 50px 50px;
    background-color: #fff;
    opacity: .99;
  }
  &__descr {
    text-transform: uppercase;
  }
  &__img {
    margin: 15px 0 0;
    border: 1px solid #000;
  }
  &__name {
    margin: 15px 0 0;
    font-family: 'futuraround';
    font-weight: 900;
    font-size: 15px;
    line-height: 25px;
    text-transform: uppercase;
    span, div {
      background-color: $color-main;
      padding: 0 3px;
    }
    span {
      display: block;
      width: fit-content;
      margin: 0 auto;
    }
    div {
      transform: translateY(-4px);
      width: fit-content;
    }
  }
  &__experience {
    color: $color-light-text;
    font-weight: 300;
    margin: 15px 0 0;
    div {
      font-size: 28px;
    }
  }
}

.first {
  background-color: #eaebec;
}

.second {
  background-color: #fff;
}