@font-face {
  font-family: 'futuraround';
  src: url("../fonts/a_futuraround.woff2") format("woff2"), url("../fonts/a_futuraround.woff") format("woff"), url("../fonts/a_futuraround.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'helveticaneuecyr';
  src: url("../fonts/helveticaneuecyr-roman.woff2") format("woff2"), url("../fonts/helveticaneuecyr-roman.woff") format("woff"), url("../fonts/helveticaneuecyr-roman.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'pfbeausanspro';
  src: url("../fonts/pfbeausanspro-black.woff2") format("woff2"), url("../fonts/pfbeausanspro-black.woff") format("woff"), url("../fonts/pfbeausanspro-black.ttf") format("ttf");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: 'pfbeausanspro';
  src: url("../fonts/pfbeausanspro-blackital.woff2") format("woff2"), url("../fonts/pfbeausanspro-blackital.woff") format("woff"), url("../fonts/pfbeausanspro-blackital.ttf") format("ttf");
  font-weight: 900;
  font-style: italic;
}

@font-face {
  font-family: 'pfbeausanspro';
  src: url("../fonts/pfbeausanspro-reg.woff2") format("woff2"), url("../fonts/pfbeausanspro-reg.woff") format("woff"), url("../fonts/pfbeausanspro-reg.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'pfbeausanspro';
  src: url("../fonts/pfbeausanspro-book.woff2") format("woff2"), url("../fonts/pfbeausanspro-book.woff") format("woff"), url("../fonts/pfbeausanspro-book.ttf") format("ttf");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'pfbeausanspro';
  src: url("../fonts/pfbeausanspro-light.woff2") format("woff2"), url("../fonts/pfbeausanspro-light.woff") format("woff"), url("../fonts/pfbeausanspro-light.ttf") format("ttf");
  font-weight: 200;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

ul {
  padding: 0;
}

.fz-20 {
  font-size: 20px;
}

html {
  font: 900 11px/20px 'pfbeausanspro';
}

body {
  margin: 0;
  color: #2a2a29;
}

.container {
  max-width: 1035px;
  padding: 0 15px;
  margin: 0 auto;
}

.garant {
  position: absolute;
  top: 51px;
  left: -77px;
  transform: rotate(-47deg);
  font-weight: 400;
  text-align: center;
  width: 300px;
  overflow: hidden;
  background: linear-gradient(180deg, #e9bb00 15%, #f5dc5e 20%, #ecbf00 37%, #f7d854 46%, #ffe100 60%, #f9eda4 65%, #f2d224 100%);
}

.garant span {
  display: block;
  font-size: 24px;
  font-weight: 400;
}

.logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100px;
}

.input {
  background-color: #353535;
  font-size: 10px;
  font-weight: 400;
  padding: 10px 13px;
  border-radius: 5px;
  width: 210px;
  color: #fff;
  border: none;
}

.input--search {
  width: 190px;
  display: block;
  margin: 15px auto 0;
}

.input--form {
  margin: 0 15px 0 0;
}

.header {
  padding: 100px 0 60px;
  background-color: #efeff0;
}

.header__items {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.header__item + .header__item {
  margin-left: auto;
}

.header__item {
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  text-align: center;
  border-bottom: 1px solid transparent;
  transition: .5s all;
}

.header__item:hover {
  border-bottom: 1px solid #000;
}

.header__item #city {
  cursor: pointer;
  border: none;
  color: #0184d0;
  text-decoration: underline;
  text-decoration-style: dotted;
  font-weight: 900;
  font-family: 'pfbeausanspro';
  background-color: #efeff0;
  text-transform: uppercase;
  font-size: 11px;
}

.header__item option {
  text-decoration: underline;
  color: #2a2a29;
}

.header__item--basket {
  font-weight: 400;
}

.header__item--basket ::before {
  content: "";
  display: block;
  width: 124px;
  height: 76px;
  position: absolute;
  top: -78px;
  left: -30px;
  background-image: url("../img/order.png");
  background-repeat: no-repeat;
}

.header__item--it {
  cursor: initial;
  font-size: 20px;
  justify-items: center;
  transform: translateX(-10px);
}

.header__item--it:hover {
  border-bottom: 1px solid transparent;
}

.header__item--call {
  position: relative;
  padding: 0 0 0 25px;
}

.header__item--call ::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  background-image: url("../img/icons/phone.png");
}

.header__basket {
  color: #0184d0;
  text-decoration: underline;
  text-decoration-style: dotted;
  font-weight: 900;
}

.header__call {
  background-color: #efeff0;
  border: none;
  border-bottom: 1px dotted #000;
  cursor: pointer;
  font-weight: 900;
  text-transform: uppercase;
  font-size: 11px;
  padding: 0;
}

.header__number {
  text-decoration: none;
  font-size: 16px;
  color: #2a2a29;
}

.header__number span {
  background-color: #fde400;
}

.header__wrapper-search {
  position: relative;
  margin: 0 auto;
}

.header__wrapper-search::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, #878d98 35%, transparent 35%, transparent 65%, #878d98 65%, #878d98 100%);
  opacity: .5;
  order: -1;
}

.header__wrapper-search::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 57%;
  width: 15px;
  height: 15px;
  transform: translateY(-50%);
  background: url("../img/icons/search.png");
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
}

.header__text {
  font-weight: 400;
  text-align: center;
  margin: 60px auto 0;
  font-size: 15px;
  position: relative;
}

.header__text div {
  font-size: 23px;
  font-weight: 900;
  font-family: 'futuraround';
  text-transform: uppercase;
  margin-bottom: 7px;
}

.header__text a {
  border-bottom: 1px dotted #0184d0;
  color: #0184d0;
  text-decoration: none;
}

.header__text::before {
  content: '';
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  height: 1px;
  opacity: .5;
  order: -1;
  background-color: #878d98;
}

.header__text::after {
  content: '';
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  height: 1px;
  opacity: .5;
  order: -1;
  background-color: #878d98;
}

.menu {
  background-color: #2b2b2b;
  padding: 20px 0;
}

.menu__items {
  display: flex;
  justify-content: center;
  list-style: none;
}

.menu__item {
  display: flex;
  align-items: flex-end;
  border-radius: 10px;
  transition: .3s all;
  margin: 0 10px;
}

.menu__item:hover {
  background-color: #fde400;
}

.menu__link {
  text-align: center;
  color: #fff;
  text-decoration: none;
  padding: 20px 25px;
}

.menu__img {
  max-height: 65px;
  max-width: 65px;
  object-fit: contain;
}

.person {
  background-color: #e0e1e2;
}

.person__wrapper {
  display: flex;
  text-transform: uppercase;
  overflow: hidden;
}

.person__block {
  position: relative;
  width: 50%;
  padding: 0 0 40px;
  overflow: hidden;
}

.person__block img {
  position: absolute;
  top: 227px;
  right: -75px;
  object-fit: contain;
  display: block;
  max-width: 430px;
  max-height: 100%;
}

.person__title {
  font-size: 59px;
  line-height: 50px;
}

.person__title div {
  font-size: 35px;
  font-weight: 400;
  margin-top: 15px;
  line-height: 40px;
}

.person__items {
  list-style-type: none;
}

.person__item {
  padding: 10px 20px;
  transition: .3s all;
  border-radius: 10px;
  width: 70%;
  cursor: pointer;
}

.person__item:hover {
  background-color: #fde400;
}

.person__legal-item:hover {
  background-color: #303030;
  color: #fff;
}

.consultant__wrapper {
  display: grid;
  grid-template: auto / 1fr 720px 285px 1fr;
}

.consultant__call {
  text-align: center;
  max-width: 722px;
  padding: 40px 50px 45px 0;
  position: relative;
  background-color: #eaebec;
}

.consultant__text {
  font-size: 15px;
  font-weight: 300;
}

.consultant__number {
  font-size: 18px;
  line-height: 35px;
  padding: 15px 0 20px;
  border: 1px double #000;
  font-family: 'futuraround';
  text-transform: uppercase;
  letter-spacing: 2px;
  width: 80%;
  margin: 30px auto 0;
}

.consultant__number a {
  font-size: 31px;
  color: #2a2a29;
  text-decoration: none;
}

.consultant__light-text {
  font-size: 29px;
  font-style: italic;
  color: #878d98;
  margin: 30px 0 0;
}

.consultant__light-text div {
  font-size: 12px;
}

.consultant__form {
  margin: 30px 0 0;
  display: flex;
  justify-content: center;
}

.consultant__btn {
  width: 112px;
  background-color: #fde400;
  border-radius: 7px;
  border: none;
  padding: 10px 13px;
  font-size: 11px;
  font-weight: 900;
  text-transform: uppercase;
}

.consultant__text-reaction {
  margin: 30px 0 0;
  font-weight: 300;
  font-size: 12px;
}

.consultant__headphones {
  position: absolute;
  right: -106px;
  top: 50%;
  transform: translateY(-50%);
}

.consultant__micro {
  position: absolute;
  right: -66px;
  top: 63.4%;
  transform: translateY(-50%);
  z-index: 5;
}

.consultant__consultant {
  text-align: center;
  max-width: 283px;
  padding: 40px 50px 50px;
  background-color: #fff;
  opacity: .99;
}

.consultant__descr {
  text-transform: uppercase;
}

.consultant__img {
  margin: 15px 0 0;
  border: 1px solid #000;
}

.consultant__name {
  margin: 15px 0 0;
  font-family: 'futuraround';
  font-weight: 900;
  font-size: 15px;
  line-height: 25px;
  text-transform: uppercase;
}

.consultant__name span,
.consultant__name div {
  background-color: #fde400;
  padding: 0 3px;
}

.consultant__name span {
  display: block;
  width: fit-content;
  margin: 0 auto;
}

.consultant__name div {
  transform: translateY(-4px);
  width: fit-content;
}

.consultant__experience {
  color: #878d98;
  font-weight: 300;
  margin: 15px 0 0;
}

.consultant__experience div {
  font-size: 28px;
}

.first {
  background-color: #eaebec;
}

.second {
  background-color: #fff;
}