.input {
  background-color: #353535;
  font-size: 10px;
  font-weight: 400;
  padding: 10px 13px;
  border-radius: 5px;
  width: 210px;
  color: #fff;
  border: none;
  &--search {
    width: 190px;
    display: block;
    margin: 15px auto 0;
  }
  &--form {
    margin: 0 15px 0 0;
  }
}